.Modal {
  --modal-size: 20rem;

  --close-x-size: 1.6rem;
  --close-x-background-color: #bbbbbb;
  --close-x-color: white;

  /* This is 2^31 - 1, the max z-index allowed by modern browsers. Use this
   * to ensure that all Modal component elements appear in front of other
   * elements in the global stacking context.
   */
  --max-z-index: 2147483647;

  box-sizing: border-box;
}

.Modal .backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  /* Since the dialog is a child element of the backdrop
   * we can use flex here to center the dialog horizontally
   * and vertically.
   */
  display: flex;
  justify-content: center;
  align-items: center;

  z-index: var(--max-z-index);

  background-color: rgba(0, 0, 0, 0.5);
}

.Modal .backdrop .dialog {
  width: var(--modal-size);
  height: var(--modal-size);

  padding: 0.6rem;
  border-radius: 0.2rem;

  background-color: white;

  position: relative;
}

/* Make 'x' to close a circular icon positioned
 * in the upper right. Center over the top-right corner
 * of the dialog (extending beyond the dialog's width and
 * height).
 */
.Modal .backdrop .dialog .close-x {
  position: absolute;
  top: calc(-1 * var(--close-x-size) / 2);
  right: calc(-1 * var(--close-x-size) / 2);

  width: var(--close-x-size);
  height: var(--close-x-size);
  border-radius: 50%;

  /* Center 'x' within the circular icon */
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: var(--close-x-background-color);
  color: var(--close-x-color);

  cursor: pointer;
}

.Modal .backdrop .dialog .scrollable {
  overflow: auto;
  height: 100%;
}

/* Make the modal full screen on mobile. This is
 * more complicated than a simple setting width and height
 * to 100% because the that would cut off the floating 'x'
 * to close icon.
 */
@media (max-width: 480px) {
  .Modal .backdrop .dialog {
    width: calc(100% - 2 * var(--close-x-size));
    height: calc(100% - 2 * var(--close-x-size));
  }
}
